<template>
    <div class="integration-design"
         v-loading="loading"
         element-loading-background="rgba(0, 0, 0, 0.7)">
        <IntePaint :config="config" :state="state"></IntePaint>
        <InteLayout :config="config" :state="state"></InteLayout>
        <InteConfig :config="config" :state="state"></InteConfig>
        <IntePublisher :config="config" :state="state.publisher"></IntePublisher>
        <InteLoader :config="config" :state="state.loader"></InteLoader>
        <Achievement :config="config" :state="state.achievement"></Achievement>
        <div class="integration-menu">
        </div>
        <div class="action">
            <el-tooltip class="item" effect="dark" content="发布" placement="right">
                <el-button type="text" @click="publish">
                    <i class="icon iconfont icon-plane"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="保存" placement="right">
                <el-button type="text" @click="save">
                    <i class="icon iconfont icon-save"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="预览" placement="right">
                <el-button type="text" @click="preview">
                    <i class="icon iconfont icon-view"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="导入" placement="right">
                <el-button type="text" @click="loader">
                    <i class="icon iconfont icon-upload"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="导出" placement="right">
                <el-button type="text" @click="exportConfig">
                    <i class="icon iconfont icon-download"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="退出" placement="right">
                <el-button type="text" @click="exit">
                    <i class="icon iconfont icon-exit"></i>
                </el-button>
            </el-tooltip>
        </div>
    </div>
</template>

<script>
    import '../assets/styles/integration/main.scss'
    import Layout from "../config/layout";
    import API from "../config/api";
    import CONST from "../config/const";
    import _ from "lodash";

    const InteLayout = () => import('@/components/integration/InteLayout')
    const InteConfig = () => import('@/components/integration/InteConfig')
    const IntePaint = () => import('@/components/integration/IntePaint')
    const IntePublisher = () => import('@/components/integration/IntePublisher')
    const InteLoader = () => import('@/components/integration/InteLoader')
    const Achievement = () => import('@/components/integration/Achievement')

    export default {
        name: "Integration",
        data() {
            return {
                loading: false,
                // 储存配置页的状态
                state: null,
                // 页面配置文件
                config: null,
            }
        },
        created() {
            this.init()
        },
        methods: {
            /**
             * 根据url参数加载配置
             */
            init() {
                this.state = {
                    layout: Layout,
                    menu: {
                        node: null,
                        path: []
                    },
                    // 配置加载弹窗
                    loader: {visible: false},
                    // 发布弹窗
                    publisher: {visible: false},
                    // 选择成果的弹窗
                    achievement: {
                        visible: false,
                        item: null,
                        url: null
                    }
                }
                this.config = {
                    // 页面id
                    id: null,
                    // 页面名称
                    name: '未命名',
                    // 封面
                    cover: null,
                    // 状态 0-未发布 / 1-已发布
                    state: 0,
                    // 布局类型
                    layout: null,
                    // 基础配置
                    base: {
                        size: {width: 1800, height: 1200},
                        backgroundColor: 'rgba(0,0,0,0)',
                        backgroundImage: null,
                        scaleMode: '0',
                        style: {
                            backgroundColor: '#545c64',
                            activeColor: null,
                            item: {
                                height: null,
                                activeColor: null
                            },
                            font: {
                                color: '#fff',
                                activeColor: '#ffd04b',
                                size: null
                            }
                        }
                    },
                    // 配置
                    navigation: null
                }

                let id = this.$route.query.id
                if (!id) {
                    this.loading = false
                    return
                }
                this.ajax({
                    url: API.INTEGRATION.GET_BY_ID,
                    params: {id: id},
                }).then(response => {
                    let config = response.data
                    if (!config) {
                        this.$message.error('错误：未找到可视化。')
                    }
                    if (typeof config.base == "string") {
                        config.base = JSON.parse(config.base)
                    }
                    if (typeof config.navigation == "string") {
                        config.navigation = JSON.parse(config.navigation)
                    }
                    this.config = config
                }).finally(() => {
                    this.loading = false
                })
            },
            /**
             * 获取当前配置的菜单节点
             */
            getCurrentMenuNode() {
                let nav = this.config.navigation
                let path = this.state.menu.path
                if (!path.length) {
                    return nav
                }
                for (let i = 0; i < path.length; i++) {
                    nav = nav[path[i]].children;
                }
                return nav
            },
            /**
             * 保存
             */
            save() {
                if (!this.config.layout) {
                    this.$message.error('错误：未选择布局。')
                    return
                }
                let config = this.formatConfigParam(this.config);
                this.ajax({
                    url: API.INTEGRATION.SAVE,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    if (!this.config.id) {
                        this.config.id = response.data.id
                    }
                    this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                    this.$router.replace({
                        query: {
                            id: this.config.id
                        }
                    })
                })
            },
            /**
             * 将可视化配置转为参数
             */
            formatConfigParam(config) {
                let param = _.cloneDeep(config)
                for (let key in param) {
                    let value = param[key]
                    if (_.isObject(value)) {
                        param[key] = JSON.stringify(value)
                    }
                }
                return param
            },
            /**
             * 发布
             */
            publish() {
                this.state.publisher.visible = true
            },
            /**
             * 预览
             */
            preview() {
                let config = this.formatConfigParam(this.config)
                this.ajax({
                    url: API.INTEGRATION.PREVIEW,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    let link = this.$router.resolve({
                        path: './integration_presentation',
                        query: {id: response.data.id}
                    })
                    window.open(link.href, '_blank')
                })
            },
            /**
             * 加载配置
             */
            loader() {
                this.state.loader.visible = true
            },
            /**
             * 导出
             */
            exportConfig() {
                let config = _.cloneDeep(this.config)
                config.id = null
                let aTag = document.createElement('a')
                let blob = new Blob([JSON.stringify(config, null, 4)])
                aTag.download = "integration-" + new Date().getTime() + ".txt"
                aTag.href = URL.createObjectURL(blob)
                aTag.click()
                URL.revokeObjectURL(blob)
            },
            /**
             * 退出编辑页面
             */
            exit() {
                this.$confirm('退出编辑界面会丢失所有未保存的操作, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$router.push('./overview')
                }).catch(() => {

                })
            }
        },
        watch: {
            /**
             * 监听组件清单，变化后重新载入组件
             */
            "state.menu.path": {
                deep: true,
                handler: function () {
                    this.state.menu.node = this.getCurrentMenuNode()
                }
            },
            /**
             * 监听组件清单，变化后重新载入组件
             */
            "config.navigation": {
                deep: true,
                handler: function () {
                    this.state.menu.node = this.getCurrentMenuNode()
                }
            }
        },
        components: {
            'InteLayout': InteLayout,
            'InteConfig': InteConfig,
            'IntePaint': IntePaint,
            'InteLoader': InteLoader,
            'IntePublisher': IntePublisher,
            'Achievement': Achievement,
        }
    }
</script>

